<template>
  <div class="gallery">
    <swiper ref="GallerySwiper" :options="swiperOptions">
      <swiper-slide v-for="(photo, i) in photos" :key="i">
        <img :srcset="photo.large_srcset" alt="" />
      </swiper-slide>

      <!-- <div class="swiper-pagination" slot="pagination"></div> -->
    </swiper>
  </div>
</template>

<script>
export default {
  props: ['photos'],
  data () {
    return {
      swiperOptions: {
        spaceBetween: 100,
        slidesPerView: 'auto'
      }
    }
  },
  mounted () {
    console.log('mounted')
    setTimeout(() => {
      this.$refs.GallerySwiper.$swiper.update()
    }, 50)
  }
}
</script>

<style lang="scss">
.gallery {
  .swiper-container {
    // height: 60vh;
    img {
      height: auto;
      min-height: inherit;
      width: 100%;
    }
  }

  .swiper-slide {
    width: 100%;
  }

  @media (min-width: 768px) {
    .swiper-container {
      // height: 60vh;
      img {
        height: 60vh;
        min-height: 400px;
        width: auto;
      }
    }
    .swiper-slide {
      width: auto;
    }
  }
}
</style>
